.App {
  text-align: center;
}

.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 300px;
  padding: 20px 20px 0px 35px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
}

a, a:visited {
  color: #52BE80;
}
html, body {
  height: 100%;  
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
a {
  text-decoration: none;
}
header {
  width: 100%;
  /* display: flex; */
  /* align-items: center; */
  -ms-flex-pack: center;
      justify-content: center;
  /* position:fixed; */
  /* top: 0; */
  /* height: 120px; */
  background-color: #2C3E50;  
}
.top-nav {
  /* background-color: black; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.top-nav .top-nav-list{
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  padding: 0;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.top-nav .top-nav-list li{
  margin-left: 20px;
  padding: 10px 0px;
}

.top-nav .top-nav-list li a{
  color: #A9CCE3;
  font-weight: bold;
  font-size: 16px;
  padding: 10px 0px;
  border-bottom: 2px solid transparent;
}
.top-nav .top-nav-list li a.active{
  color: #A9CCE3;
  border-bottom-color: #A9CCE3;
}

footer {
  color: white;
  background-color: #2C3E50;
  height: 120px;
  padding: 10px;
}

main {
  background: #2C3E50;
  /* height: 400px; */
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

main .page-content {
  padding: 20px;
}

.page-title {
  margin: 0;
  padding: 20px;
}

.exam-content .progress-bar {
  width: 80%;
  margin: auto;
}

.answers {
    padding: 0;
    list-style: none;
    text-align: left;
    float: none;
    margin: 0 auto;
}

.answers li {
  padding: 15px;
}

.exam-content .question-answer-container .answer-form {
  float: none;
  margin: auto;
  display: inline-block;
}

.exam-content .question-answer-container {
  font-size: 20px;
  padding: 20px;
}

.cont-btn {
  float: none;
  margin: 0 auto;
  /*text-align: center;*/
}
.answers li.correctAnswer, .answers li.incorrectAnswer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.answers li.correctAnswer {
  background: #409628;
}
.answers li.correctAnswer label::after {
  content:'\f00c';
  font-family: FontAwesome;
  /* margin: auto;
  margin-right: 10px;
  margin-left: 20px; */
}

.answers li label::after {
  margin: auto;
  margin-right: 0;
  margin-left: 20px;
}

.answers li.incorrectAnswer {
  background: #EC7063;
}
.answers li.incorrectAnswer label::after {
  content:'\f00d';
  font-family: FontAwesome;
  /* &#xf057; for circle with x */
}

.exam-stats {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.exam-stats .grade-circle {
  height: 150px;
  width: 150px;
  position: relative;
}
.exam-stats .grade-circle span {
  font-size: 40px;
  margin: auto;
  position: absolute;
  top: 50%; left: 50%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}
.exam-stats .grade-circle .grade-passed {
  color: #409628;
}

.exam-stats .grade-circle .grade-failed {
  color: #EC7063;
}

.exam-stats .num-correct-stats {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
  padding-left:10px;
}
hr {
  width: 100%;
  border-width: 1px;
  border-style: solid;
  color: lightgray;
}

.exam-stats .num-correct-stats div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
}

.exam-stats .num-correct-stats #numCorrect, 
.exam-stats .num-correct-stats #numIncorrect {
  font-size: 30px;
  font-weight:bold;
}
.exam-stats .num-correct-stats #correctText, 
.exam-stats .num-correct-stats #incorrectText {
  font-size: 15px;
  color: gray;
}
.exam-stats .num-correct-stats #numCorrect {
  color: #409628;
}
.exam-stats .num-correct-stats #numIncorrect {
  color: #EC7063;
}

.exam-questions-container .question-answer-container .answer-form, 
.exam-questions-container .question-answer-container .answer-form .answers{
  width: 75%
}

input[type=radio] {
  /* border: 0px;
  width: 100%;
  height: 2em; */
}
.answers li label {
  display: -ms-flexbox;
  display: flex;
}
.answers li label input {
  margin: auto 0;
}

.answers li label span {
  margin: auto;
  margin-left: 30px;
  /* width: 80%; */
}

.drivers-guide-object {
  width: 100%;
  height: 300px;
  /* border: 5px solid #666; */
}

@media (max-width: 800px) {
  .App-logo {
    padding-left: 35px;
    padding-right: 0px;
  }
  .top-nav .top-nav-list{
    -ms-flex-pack: center;
        justify-content: center;
  }
  .exam-questions-container .question-answer-container .answer-form, 
  .exam-questions-container .question-answer-container .answer-form .answers {
    width: 100%;
  }

  .drivers-guide-object {
    width: 100%;
    height: 500px;
    /* border: 5px solid #666; */
  }
}


